/* eslint-disable */
import React, {Component} from 'react';
import IconTrashCan from "../../assets/trash-can.svg";
import moment from 'moment'
import 'moment/locale/ru'
moment.locale('ru');

class Message extends Component {
	constructor(props) {
		super(props);

		this.state = {
			text: false
		};
	}

	appearMessage = () => {
		let { message } = this.props.message;
		message = (
			<tr>
				<td className={'message-text'} colSpan={6}>{message}</td>
			</tr>
		);

		return this.state.text ? message : null;
	};

	deleteMessage = id => { this.props.deleteMessage(id) };

	render() {
		let { _id, date, name, phone, email } = this.props.message;
		return (
			<tbody>
				<tr>
					<td>{moment(date).fromNow()}</td>
					<td>{name}</td>
					<td>{phone}</td>
					<td>{email}</td>
					<td>
						<button className={'toggle-btn pointer py-10'} onClick={() => { this.setState({text: !this.state.text}) }}></button>
					</td>
					<td>
						<button className={'remove-btn pointer p-10'} onClick={() => { this.deleteMessage(_id) }}>
							<img src={IconTrashCan} className={'h-32 w-32'}/>
						</button>
					</td>
				</tr>
				{this.appearMessage()}
			</tbody>
		);
	}
}

export default Message;
