/* eslint-disable */
import axios from 'axios'
import {
	ADD_TO_COLLECTION,
	SERVER_ERROR,
	REMOVE_FROM_COLLECTION
} from "../constants/actionTypes";


export const ADD_ITEM = item => {
	return (dispatch) => {
		axios.post('/api/messages/add', item)
			.then(() => {
				dispatch({
					type: ADD_TO_COLLECTION
				})
			})
			.catch(err => {
				dispatch({
					type: SERVER_ERROR,
					error: err
				})
			})
	}
}

export const REMOVE_ITEM = (id, callback) => {
	return (dispatch) => {
		axios.post('/api/messages/remove', id)
			.then(() => {
				dispatch({
					type: REMOVE_FROM_COLLECTION
				});
				callback();
			})
			.catch(err => {
				dispatch({
					type: SERVER_ERROR,
					error: err
				})
			})
	}
}

export const GET_COLLECTION = (callback) => {
	return (dispatch) => {
		axios.get('/api/messages/collection')
			.then(res => {
				if (res.status === 403) {
					throw new Error(" !")
				} else {
					return res.data;
				}
			})
			.then(collection => {
				dispatch({
					type: "GET_COLLECTION",
					collection
				});
				callback();
			})
			.catch(err => {
				dispatch({
					type: SERVER_ERROR,
					error: err
				})
			})
	}
}