/* eslint-disable */
import {
	SIGNIN_ERROR,
	SIGNOUT_ERROR,
	SIGNIN_SUCCESS,
	SIGNOUT_SUCCESS
} from "../constants/actionTypes";


const initState = {
	loggedIn: null
};

export default ( state = initState, action ) => {
	switch ( action.type ) {
		case SIGNIN_ERROR:
			return {
				...state,
				signInError: action.signInError
			};
		case SIGNOUT_ERROR:
			return {
				...state,
				signOutError: action.signOutError
			};
		case SIGNIN_SUCCESS:
			return {
				...state,
				loggedIn: action.loggedIn
			};
		case SIGNOUT_SUCCESS:
			return {
				...state,
				loggedIn: action.loggedIn
			};
		default:
			return state;
	}

	return state;
}
