/* eslint-disable */
import {
	SERVER_ERROR,
	ADD_TO_COLLECTION,
	REMOVE_FROM_COLLECTION,
	GET_COLLECTION
} from "../constants/actionTypes";


const initState = {
	collection: null
};

export default ( state = initState, action ) => {
	switch ( action.type ) {
		case SERVER_ERROR:
			return {
				...state,
				error: action.error
			};
		case ADD_TO_COLLECTION:
			return state;
		case REMOVE_FROM_COLLECTION:
			return state;
		case GET_COLLECTION:
			return {
				...state,
				collection: action.collection
			};
		default:
			return state;
	}

	return state;
}
