/* eslint-disable */
import {
	SWITCH_LANGUAGE
} from "../constants/actionTypes";

const initState = {
	lang: window.localStorage.getItem("user_language") || "RU"
};

export default ( state = initState, action ) => {
	switch ( action.type ) {
		case SWITCH_LANGUAGE:
			return {
				...state,
				lang: action.lang
			};
		default:
			return state;
	}

	return state;
}
