/* eslint-disable */
import React from 'react';

export const withUtils = ComposedComponent => class extends React.Component {

	static displayName = 'withUtilsHOC';

	constructor(props) {
		super(props);
		this.animated_elements = null;
		this.single_element = null;
	};

	componentDidMount() {
		window.addEventListener('scroll', this.setAnimations, false);
		window.addEventListener('scroll', this.shift_y, false);
	};

	componentWillUnmount() {
		window.removeEventListener('scroll', this.setAnimations);
		window.removeEventListener('scroll', this.shift_y);
	};

	getRefs = (refs, single) => {
		if (single) {
			this.single_element = refs;
			console.log(this.single_element.current);
		} else {
			this.animated_elements = refs;
		}

	};

	setAnimations = () => {
		if (this.animated_elements) {
			this.animated_elements.forEach((element) => {
				this.checkVisibility(element);
			})
		}
	};

	checkVisibility = ({current: element}) => {
		let position = element.getBoundingClientRect();
		let threshold_value = element.getBoundingClientRect().top - window.innerHeight;

		// checking for partial visibility
		if (element.classList.contains('appear-animation')) {
			if (threshold_value > 50) {
				element.classList.remove('appear-animation');
			}
		} else if (position.top < window.innerHeight && position.bottom >= 0) {
			if (!element.classList.contains('appear-animation')) element.classList.add('appear-animation');
		}

	};

	// TODO:  
	shift_y = () =>{
		if (!!this.single_element) {
			let {current: element} = this.single_element;

			let height = element.offsetHeight;
			let scroll_value = window.scrollY;

			if (scroll_value <= height) {
				let translate_value = scroll_value / 10;
				element.style.transform = `translateY(-${translate_value}px)`;
			}
		}

	}

	render() {
		return <ComposedComponent setRefs={this.getRefs} {...this.props} {...this.state} />;
	}
};