/* eslint-disable */
import React, {Component} from 'react';
import { withUtils } from "./HOCs/withUtils";


class AboutSection extends Component {
	constructor(props) {
		super(props);
		this.textRef = React.createRef();
		this.animated_elements = [
			this.textRef
		];
	}

	componentDidMount() {
		this.props.setRefs(this.animated_elements);
	}

	render() {
		return (
			<section id={'about-section'} className="centered py-100 px-15">
				<div className="text flex-column flex-align-center fade-up" ref={this.textRef}>
					<span className="title"> </span>
					<div className="hr-line mt-20 w-50" />
					<span className="description text-center mt-20">
						   -  .   2008 .   :          .          ,         .          .        .                ,   .  -      .            . .  2018 .    . .
					</span>
				</div>
			</section>
		);
	}
}

export default withUtils(AboutSection);