/* eslint-disable */
import React, {Component} from 'react';
import { YMaps, Map, Placemark, ZoomControl } from "react-yandex-maps";

const MAP_PARAMS = {
	map_props: {
		style: {
			flex: "1",
			maxWidth: "100%",
			height: "600px"
		},
		defaultState: {
			center: [53.540046, 49.445106],
			zoom: 16
		}
	},
	placemark_props: {
		geometry: [53.540046, 49.445106],
		options: {
			preset: "islands#redDotIcon"
		},
		properties: {
			iconCaption: " , 8"
		}
	},
	zoom_control_props: {
		options: {
			size: 'large',
			zoomDuration: 1000
		}
	}

};

class MapSection extends Component {
	constructor(props) {
		super(props);
		this.state = {
			map_cover: true,
			map_appears: false
		};

		this.SECTION = React.createRef();
	}

	hideMapCoverBox = () => {
		this.setState({ map_cover: false })
	}

	handleClickOutside = ({ target }) => {
		let { current } = this.SECTION;
		!current.contains(target) && this.setState({ map_cover: true });
	}

	componentDidMount() {
		document.addEventListener('mousedown', this.handleClickOutside, false);
	}

	componentWillUnmount() {
		document.removeEventListener('mousedown', this.handleClickOutside, false);
	}

	render() {
		let { map_props, placemark_props, zoom_control_props } = MAP_PARAMS;
		let { map_cover, map_appears } = this.state;
		return (
			<section id={'map-section'} className="relative" ref={this.SECTION}>
				<YMaps>
					{map_appears && <div className="map-cover-box absolute centered" style={{display: !map_cover && "none"}}>
						<button className="pointer" onClick={this.hideMapCoverBox}>
							 
						</button>
					</div>}
					<Map
						{...map_props}
						onLoad={() => this.setState({ map_appears: true })}
					>
						<ZoomControl
							{...zoom_control_props}
						/>
						<Placemark
							{...placemark_props}
						/>
					</Map>
				</YMaps>
			</section>
		);
	}
}

export default MapSection;