/* eslint-disable */
import axios from 'axios'
import {
	SIGNIN_SUCCESS,
	SIGNIN_ERROR,
	SIGNOUT_SUCCESS,
	SIGNOUT_ERROR
} from "../constants/actionTypes";


export const SIGNIN = credentials => {
	return (dispatch) => {
		axios
			.post('/api/admin/sign_in', credentials)
			.then(res => {
				if (res.status === 200) {
					dispatch({
						type: SIGNIN_SUCCESS,
						loggedIn: true
					})
				}
			})
			.catch(err => {
				dispatch({
					type: SIGNIN_ERROR,
					signInError: err
				})
			})
	}
}

export const SIGNOUT = () => {
	return (dispatch) => {
		axios
			.get('/api/admin/sign_out')
			.then((res) => {
				if (res.status === 200) {
					dispatch({
						type: SIGNOUT_SUCCESS,
						loggedIn: false
					})
				} else {
					throw new Error("     !")
				}
			})
			.catch(err => {
				dispatch({
					type: SIGNOUT_ERROR,
					signOutError: err
				})
			})
			
	}
}

export const CHECK_AUTH = () => {
	return (dispatch) => {
		axios
			.get('/api/admin/check')
			.then(res => {
				if (res.status === 200) {
					dispatch({
						type: SIGNIN_SUCCESS,
						loggedIn: !!res.data
					})
				}
			})
	}
}