/* eslint-disable */
import React, {Component} from 'react';


class Footerbar extends Component {

	scrollIntoView = (element_id) => {
		let element = document.querySelector(`#${element_id}`);
		let mobile_nav = document.querySelector('#page-mobile-navbar');
		let navHeight = document.querySelector('#page-navbar').offsetHeight;
		//     
		if (getComputedStyle(mobile_nav).display !== "none") {
			navHeight = mobile_nav.offsetHeight
		} else if (window.pageYOffset < 100) {
			//  10px     
			navHeight -= 10;
		}

		let scroll_position = element.getBoundingClientRect().top + window.scrollY - navHeight;

		element.scrollIntoView({behavior: "smooth"});
		window.scroll({
			left: 0,
			top: scroll_position,
			behavior: 'smooth'
		})
	};

	render() {
		return (
			<footer id={'footerbar'} className="centered py-40 px-15">

				<div className="content flex-space-between">

					<div className="copyright flex-column">
						<span className={'title'}>Delta P</span>
						<span className={'mt-10 caption'}> 2018 Faraks-01</span>
					</div>

					<ul className="menu flex-align-center mx-10">
						<li className="item item-1 mx-8 pointer" onClick={() => { this.scrollIntoView('homepage-section')} }></li>
						<li className="item item-2 mx-8 pointer" onClick={() => { this.scrollIntoView('about-section')} }> </li>
						<li className="item item-3 mx-8 pointer" onClick={() => { this.scrollIntoView('word-directions-section')} }> </li>
						<li className="item item-4 mx-8 pointer" onClick={() => { this.scrollIntoView('feedback-section')} }>  </li>
						<li className="item item-5 mx-8 pointer" onClick={() => { this.scrollIntoView('info-section')} }></li>
					</ul>

				</div>

			</footer>
		);
	}
};

export default Footerbar;
