/* eslint-disable */
import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { connect } from 'react-redux';
import { SWITCH_LANGUAGE } from "../actions/langActions";

import ImgLogo from "../assets/logo.png";

import {ReactComponent as IconBurgerMenu} from "../assets/burger-menu.svg";
import {ReactComponent as IconAdmin} from "../assets/admin-with-cogwheels.svg";
import {ReactComponent as IconLocation} from "../assets/location.svg";
import {ReactComponent as IconPhone} from "../assets/phone.svg";
import {ReactComponent as IconCross} from "../assets/cross.svg";

const mapStateToProps = state => {
	return {
		lang: state.lang.lang,
		loggedIn: state.auth.loggedIn
	}
};

const mapDispatchToProps = dispatch => {
	return {
		SWITCH_LANGUAGE: language => {
			dispatch(SWITCH_LANGUAGE(language))
		}
	};
};


class MobileNavbar extends Component {
	constructor(props) {
		super(props);

		this.state = {
			menu_hidden: true
		};
	}

	// switchLanguage = () => {
	// 	let language = this.props.lang === "RU" ? "EN" : "RU";
	// 	this.props.SWITCH_LANGUAGE(language)
	// };

	scrollIntoView = (element_id) => {
		let element = document.querySelector(`#${element_id}`);
		let mobile_nav = document.querySelector('#page-mobile-navbar');
		let navHeight = document.querySelector('#page-navbar').offsetHeight;
		//     
		if (getComputedStyle(mobile_nav).display !== "none") {
			navHeight = mobile_nav.offsetHeight
		} else if (window.pageYOffset < 100) {
			//  10px     
			navHeight -= 10;
		}

		let scroll_position = element.getBoundingClientRect().top + window.scrollY - navHeight;

		element.scrollIntoView({behavior: "smooth"});
		window.scroll({
			left: 0,
			top: scroll_position,
			behavior: 'smooth'
		})
	};

	renderMenu = () => {
		let admin_btn_visibility = this.props.loggedIn ? {display: "block"} : {display: "none"};

		let hidden = this.state.menu_hidden ? 'hidden' : '';

		return (
			<div className={`menu centered fixed ${hidden}`}>
				<div className={'close-btn absolute pointer'} onClick={this.toggleMenu}>
					<IconCross className={'h-30 w-30'} />
				</div>
				<ul className="menu-list flex-column flex-align-center">
					<li className="item item-1 my-10 pointer" onClick={() => { this.toggleMenu(); this.scrollIntoView('homepage-section')} }></li>
					<li className="item item-2 my-10 pointer" onClick={() => { this.toggleMenu(); this.scrollIntoView('about-section')} }> </li>
					<li className="item item-3 my-10 pointer" onClick={() => { this.toggleMenu(); this.scrollIntoView('word-directions-section')} }> </li>
					<li className="item item-4 my-10 pointer" onClick={() => { this.toggleMenu(); this.scrollIntoView('feedback-section')} }>  </li>
					<li className="item item-5 mt-40 pt-15">
						<IconPhone className={'h-28 w-28 mx-7 pointer'} onClick={() => { this.toggleMenu(); this.scrollIntoView('info-section') } } />
						<IconLocation className={'h-28 w-28 mx-7 pointer'} onClick={() => { this.toggleMenu(); this.scrollIntoView('map-section') }} />
						<Link className={'admin-btn w-fit h-fit mx-7 pointer'} style={admin_btn_visibility} to='/admin'><IconAdmin className={"h-28 w-28"} /></Link>
					</li>
				</ul>
			</div>
		)
	};

	toggleMenu = () => {
		this.setState({
			menu_hidden: !this.state.menu_hidden
		});
	};

	render() {
		let { lang, loggedIn } = this.props;
		let langBtnText = lang === "RU" ? "EN" : "RU";
		// TODO:  ,  
		let admin_btn_visibility = loggedIn ? {display: "block"} : {display: "none"};

		return (
			<nav id={'page-mobile-navbar'} className='fixed h-60 centered p-10'>
				<div className="item-1">
					<button className={'toggle-btn p-0 pt-2 w-fit h-fit pointer mr-auto'} onClick={this.toggleMenu}><IconBurgerMenu className={"h-20 w-20"} /></button>
				</div>
				<div className={'item-2 logo flex-justify-center mx-auto'}><img className={'h-30 pointer'} src={ImgLogo} onClick={() => { this.scrollIntoView('homepage-section')} } /></div>
				<div className="item-3 icons flex-end flex-align-center ml-auto">
					<IconPhone className={'h-14 w-14 mx-7 pointer'} onClick={() => { this.scrollIntoView('info-section') } } />
					<IconLocation className={'h-14 w-14 mx-7 pointer'} onClick={() => { this.scrollIntoView('map-section') }} />
					<Link className={'admin-btn w-fit h-fit mx-7 pointer'} style={admin_btn_visibility} to='/admin'><IconAdmin className={"h-14 w-14"} /></Link>
				</div>
				{this.renderMenu()}
			</nav>
		);
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(MobileNavbar);



