/* eslint-disable */
const inputChecker = {
  name(str) {
    const regex = /^[a-z- ,.'-]+$/i;
    let match;
    let length = 2 < str.length && str.length <= 50;

    if ((match = regex.test(str)) !== null) {
      return (match && length);
    } else {
      return (match);
    }
  },

  phone(str) {
    return str.length > 8 && str.length < 20;
  },

  email(str) {
    const regex = /^[a-zA-Z]+[^@]*@[a-zA-Z]+[^@]*\.[a-zA-Z]{2,}[^@]*$/;
    let match;
    let length = 7 < str.length && str.length <= 30;

    if ((match = regex.test(str)) !== null) {
      return (match && length);
    } else {
      return (match);
    }
  },

  message(str) {
    // let length = true;
    return true;
  }
}

export default inputChecker;
