/* eslint-disable */
import React from 'react';
import {withUtils} from "../HOCs/withUtils";

import IconWorldmap from "../../assets/worldmap.svg";
import {ReactComponent as IconClocks} from "../../assets/clocks.svg";
import {ReactComponent as IconSmartphone} from "../../assets/smartphone.svg";
import {ReactComponent as IconEnvelope} from "../../assets/envelope.svg";

class InfoSection extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};

		this.textRef = React.createRef();
		this.animated_elements = [
			this.textRef
		];
	}

	componentDidMount() {
		this.props.setRefs(this.animated_elements);
	}

	render() {
		return (
			<section id={'info-section'} className="centered py-75 px-15">
					<div className="content flex-column flex-align-center fade-up" ref={this.textRef}>
						<span className="title text-center"> </span>
						<div className={'hr-line w-50 mt-20 mb-10'} />
						<span className="text mt-14">     - , ,   .</span>
						<ul className="card-list mt-15">
							<li className="card card-1 flex-column flex-align-center">
								<div className="icon-wrapper p-14">
									<div className="worldmap-icon-wrapper w-82 h-82">
										<div className="img-group">
											<img className="w-142 h-79 icon-1" src={IconWorldmap} />
											<img className="w-142 h-79 icon-2" src={IconWorldmap} />
										</div>
									</div>
								</div>
								<span className="title mt-13"></span>
								<hr className="my-15" />
								<span className="text text-1">, . ,</span>
								<span className="text text-2 mt-5">. , 8</span>
							</li>
							<li className="card card-2 flex-column flex-align-center">
								<div className="icon-wrapper w-80 h-80 p-14">
									<IconClocks />
								</div>
								<span className="title mt-13"></span>
								<hr className="my-15" />
								<span className="text text-1">-: c 9.00 - 19.00</span>
								<span className="text text-2 mt-5">-:  10.00 - 16.00</span>
							</li>
							<li className="card card-3 flex-column flex-align-center">
								<div className="icon-wrapper w-95 h-88 p-6">
									<IconSmartphone />
								</div>
								<span className="title mt-13"></span>
								<hr className="my-15" />
								<span className="text text-1">+7 (916) 652-33-20</span>
								<span className="text text-2 mt-5">+7 (965) 210-55-77</span>
							</li>
							<li className="card card-4 flex-column flex-align-center">
								<div className="icon-wrapper w-64 h-90 p-20">
									<div>
										<IconEnvelope />
									</div>
								</div>
								<span className="title mt-13">E-MAIL</span>
								<hr className="my-15" />
								<span className="text text-1">info@delta-p.msk.ru</span>
							</li>
						</ul>
					</div>
				</section>
		);
	}
}

export default withUtils(InfoSection);