/* eslint-disable */
import React, {Component} from 'react';
import IconLogout from "../../assets/logout.svg";
import {connect} from "react-redux";
import {
	SIGNIN,
	SIGNOUT
} from "../../actions/authActions";

const mapStateToProps = state => {
	return {
		loggedIn: state.auth.loggedIn
	}
};

const mapDispatchToProps = dispatch => {
	return {
		SIGNIN: credentials => {
			dispatch(SIGNIN(credentials))
		},
		SIGNOUT: () => {
			dispatch(SIGNOUT())
		}
	};
}

class Login extends Component {
	constructor(props) {
		super(props);

		this.state = {
			username: "",
			password: ""
		}
	}

	componentDidMount() {
		if (this.props.loggedIn) this.props.history.push('/admin/messages');
	}

	componentDidUpdate() {
		if (this.props.loggedIn) this.props.history.push('/admin/messages');
	}

	handleInputValue = (event) => {
		let { name, value } = event.currentTarget;
		this.setState({
			[name]: value
		});
	};

	handleSignIn = (event) => {
		event.preventDefault();
		let { username, password } = this.state;
		this.props.SIGNIN({ username, password });
	};

	handleSignOut = (event) => {
		this.props.SIGNOUT();
	};

	render() {
		return (
			<form className="login-form m-auto" onSubmit={this.handleSignIn}>
				<div className="input-group flex-column flex-align-center">
					<label className="label label-1 flex-column flex-align-center">
						<span className="title mb-8"></span>
						<input className="px-16 py-8" type="text" name="username" onChange={this.handleInputValue}/>
						<span className="error-text"></span>
					</label>
					<label className="label label-2 flex-column flex-align-center mt-12">
						<span className="title mb-8"></span>
						<input className="px-16 py-8" type="text" name="password" onChange={this.handleInputValue}/>
						<span className="error-text"></span>
					</label>
				</div>
				<div className="btns-group flex flex-space-evenly mt-16">
					<button className="btn btn-1 pointer" type="submit">
						
					</button>
					<button className="btn btn-2 pointer" onClick={this.handleSignOut}>
						<img className="h-28 w-30" src={IconLogout}/>
					</button>
				</div>
			</form>
		);
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Login)
