/* eslint-disable */
import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { connect } from 'react-redux';
import { SWITCH_LANGUAGE } from "../actions/langActions";


import LogoDeltaP from "../assets/logo.png";
import {ReactComponent as IconPhone} from "../assets/phone.svg";
import {ReactComponent as IconLocation} from "../assets/location.svg";
import {ReactComponent as IconUser} from "../assets/user.svg";

import {ReactComponent as IconAdmin} from "../assets/admin-with-cogwheels.svg";

const mapStateToProps = state => {
	return {
		lang: state.lang.lang,
		loggedIn: state.auth.loggedIn
	}
};

const mapDispatchToProps = dispatch => {
	return {
		SWITCH_LANGUAGE: language => {
			dispatch(SWITCH_LANGUAGE(language))
		}
	};
};

class Navbar extends Component {
	constructor(props) {
		super(props);

		this.state = {
			scrolled: false
		}
	}

	switchLanguage = () => {
		// let language = this.props.lang === "RU" ? "EN" : "RU";
		// this.props.SWITCH_LANGUAGE(language)
	};

	scrollIntoView = (element_id) => {
		let element = document.querySelector(`#${element_id}`);
		let mobile_nav = document.querySelector('#page-mobile-navbar');
		let navHeight = document.querySelector('#page-navbar').offsetHeight;
		//     
		if (getComputedStyle(mobile_nav).display !== "none") {
			navHeight = mobile_nav.offsetHeight
		} else if (window.pageYOffset < 100) {
			//  10px     
			navHeight -= 10;
		}

		let scroll_position = element.getBoundingClientRect().top + window.scrollY - navHeight;

		element.scrollIntoView({behavior: "smooth"});
		window.scroll({
			left: 0,
			top: scroll_position,
			behavior: 'smooth'
		})
	};

	checkYScroll = () => {
		let scroll_position = window.scrollY;
		if (scroll_position > 90) {
			this.setState({scrolled: true});
		} else if (scroll_position < 110) {
			this.setState({scrolled: false});
		}
	};


	componentDidMount() {
		document.addEventListener('scroll', this.checkYScroll, false);
	};

	componentWillUnmount() {
		document.removeEventListener('scroll', this.checkYScroll);
	};


	render() {
		let { lang, loggedIn } = this.props;
		let langBtnText = lang === "RU" ? "EN" : "RU";

		// TODO:  ,  
		let adminBtnVisibility = loggedIn ? {display: "list-item"} : {display: "none"};

		let scrolled = this.state.scrolled ? 'scrolled' : '';

		return (
			<nav id={'page-navbar'} className={`fixed px-15 ${scrolled}`}>
				<ul className="menu flex-end flex-align-center">
					<li className="item item-1 mr-auto">
						<img className={'main-logo'} src={LogoDeltaP}/>
					</li>
					<li className="item item-2 mx-15 pointer" onClick={() => { this.scrollIntoView('homepage-section')} }></li>
					<li className="item item-3 mx-15 pointer" onClick={() => { this.scrollIntoView('about-section')} }> </li>
					<li className="item item-4 mx-15 pointer" onClick={() => { this.scrollIntoView('word-directions-section')} }> </li>
					<li className="item item-5 mx-15 pointer" onClick={() => { this.scrollIntoView('feedback-section')} }>  </li>
					<li className="item item-6 mx-8 pointer"  onClick={() => { this.scrollIntoView('info-section') } }>
						<IconPhone className={'h-14 w-14'} />
					</li>
					<li className="item item-7 mx-8 pointer" onClick={() => { this.scrollIntoView('map-section') }}>
						<IconLocation className={'h-14 w-14'} />
					</li>
					<li className="item item-8 mx-8 pointer" style={adminBtnVisibility}>
						<Link to='/admin'>
							<IconUser className={'h-14 w-14'} />
						</Link>
					</li>
				</ul>
			</nav>
		);
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);



