/* eslint-disable */
import React, {Component} from 'react';
import IconLoop from "../../assets/loop.svg";
import MessageInstance from "./Message";
import {connect} from 'react-redux';
import {ADD_ITEM, GET_COLLECTION, REMOVE_ITEM} from "../../actions/messagesActions";

const mapStateToProps = state => {
	return {
		messages: state.messages.collection
	};
};

const mapDispatchToProps = dispatch => {
	return {
		ADD_ITEM: item => {
			dispatch(ADD_ITEM(item))
		},
		REMOVE_ITEM: (id, callback) => {
			dispatch(REMOVE_ITEM(id, callback))
		},
		GET_COLLECTION: (callback) => {
			dispatch(GET_COLLECTION(callback))
		}
	};
};

class MessagesPage extends Component {
	constructor(props) {
		super(props);

		this.state = {
			items: {},
			currentMessages: undefined,
			receivedItemsCounter: 0
		};

		this.timer_id = null;
	}

	receiveFreshData = () => {
		this.props.GET_COLLECTION();
		this.timer_id = setTimeout(this.receiveFreshData, 20000);
	};


	componentDidMount = () => {
		this.receiveFreshData();
	};

	componentWillUnmount = () => {
		clearTimeout(this.timer_id);
	};

	componentDidUpdate(prevProps) {
		let { currentMessages, receivedItemsCounter } = this.state;
		let { messages } = this.props;
		if (messages) {
			if (currentMessages === undefined) {
				this.setState({ currentMessages: [...messages] })
			} else if (typeof currentMessages === "object") {
				if (currentMessages.length !== messages.length) {
					if (receivedItemsCounter !== (messages.length - currentMessages.length)) {

						let counter = (messages.length - currentMessages.length);

						counter = counter > 99 ? 99 : counter;

						this.setState({ receivedItemsCounter: counter });
					}
				}
			};
		}
	};

	updateMessages = () => {
		this.setState({
			currentMessages: [...this.props.messages],
			receivedItemsCounter: 0
		});
	};

	deleteMessage = _id => {
		this.props.REMOVE_ITEM({ _id }, () => {
			this.props.GET_COLLECTION(
				() => {
					this.setState({
						currentMessages: [...this.props.messages],
						receivedItemsCounter: 0
					});
				}
			)
		});

	};

	insertMessages = () => {
		let { currentMessages } = this.state;
		let { messages } = this.props;

		if (currentMessages) {
			return currentMessages.map((message, index) => <MessageInstance deleteMessage={this.deleteMessage} message={message} key={`M#${index + 1}`} />)
		} else if (messages) {
			return messages.map((message, index) => <MessageInstance deleteMessage={this.deleteMessage} message={message} key={`M#${index + 1}`} />)
		} else {
			return null
		}
	};

	render() {
		let { receivedItemsCounter } = this.state;
		let badgeVisibility = receivedItemsCounter > 0 ? '' : 'd-none';
		return (
			<section className='messages-section flex-column p-20'>
				<div className="btns-panel flex-space-between mb-10 ml-auto">
					<button onClick={this.updateMessages} className={'refresh-btn pointer px-16 py-8'}>
						<span className={`counter centered w-24 h-24 ${badgeVisibility}`}>{receivedItemsCounter}</span>
						<img className={'w-32 h-32'} src={IconLoop}/>
					</button>
				</div>

				<table className={'messages-table'}>
					<thead>
						<tr>
							<th></th>
							<th></th>
							<th></th>
							<th></th>
							<th></th>
							<th className={'w-60'}></th>
						</tr>
					</thead>

					{this.insertMessages()}

				</table>
			</section>
		);
	}
}

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(MessagesPage);