/* eslint-disable */
import React from 'react';
import {withUtils} from "./HOCs/withUtils";

import IconWaterDrop from "../assets/water-drop.svg";
import IconGears from "../assets/gears.svg";
import IconRecycle from "../assets/recycle.svg";
import IconFlask from "../assets/flask.svg";

class WorkDirectionsSection extends React.Component {
	constructor(props) {
		super(props);
		this.textRef1 = React.createRef();
		this.textRef2 = React.createRef();
		this.animated_elements = [
			this.textRef1,
			this.textRef2
		];
	}

	componentDidMount() {
		this.props.setRefs(this.animated_elements);
	}

	render() {
		return (
			<section id={'word-directions-section'} className="flex-column">
				<div className="part-1 centered py-100 px-15">
					<div className="content flex-column flex-align-center fade-up" ref={this.textRef1}>
						<span className="title text-center"> </span>
						<div className="hr-line mt-20 w-50" />
						<ul className="card-list flex-space-between mt-60">
							<li className="card card-1 flex-column flex-align-center">
								<img src={IconWaterDrop} className="icon w-66 h-66"/>
								<span className="title mt-40"></span>
								<span className="caption mt-10 text-center mb-20">
						    ,   
					</span>
								<span className="link centered pointer mt-auto px-12 py-6"></span>
							</li>
							<li className="card card-2 flex-column flex-align-center">
								<img src={IconGears} className="icon w-66 h-66"/>
								<span className="title mt-40"></span>
								<span className="caption mt-10 text-center mb-20">
						      
					</span>
								<span className="link centered pointer mt-auto px-12 py-6"></span>
							</li>
							<li className="card card-3 flex-column flex-align-center">
								<img src={IconRecycle} className="icon w-66 h-66"/>
								<span className="title mt-40"></span>
								<span className="caption mt-10 text-center mb-20">
						  ,   ,     
					</span>
								<span className="link centered pointer mt-auto px-12 py-6"></span>
							</li>
							<li className="card card-4 flex-column flex-align-center">
								<img src={IconFlask} className="icon w-66 h-66"/>
								<span className="title mt-40"></span>
								<span className="caption mt-10 text-center mb-20">
						       
					</span>
								<span className="link centered pointer mt-auto px-12 py-6"></span>
							</li>
						</ul>
					</div>
				</div>

				<div className="part-2 centered px-15">
					<div className="content flex-column flex-align-center fade-up" ref={this.textRef2}>
						<span className="title text-center">     ,   ,  :</span>
						<ul className="list flex-column flex-align-center mt-10">
							<li className="item item-1 text-center mt-20"> </li>
							<li className="item item-2 text-center mt-20"> </li>
							<li className="item item-3 text-center mt-20">  </li>
							<li className="item item-4 text-center mt-20">    </li>
						</ul>
					</div>
				</div>
			</section>
		);
	}
}


export default withUtils(WorkDirectionsSection);